*,
*::after,
*::before{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: #d8e5f7;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

a{
  text-decoration: none;
}

.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background-color: #9754CB;
  z-index: 3;
}
.header_content{
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1100px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.logo{
  font-size: 30px;
  font-weight: 400;
  color: #fff;
}

.logo img{
  max-width: 200px;
  max-height: 50px;
}

.nav-menu{
  display: flex;
  align-items: center;
  font-size: 22px;
  justify-self: flex-start;
}

.about{
  color: white;
}

.profile{
  line-height: 0;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
  height: 100%;
  padding: 0 20px;
}

.profile.active{
  box-shadow: inset 6px 6px 10px rgba(0, 0, 0, 0.1),
              inset 6px -6px 10px rgba(0, 0, 0, 0.1),
              inset -6px -6px 10px rgba(0, 0, 0, 0.1),
              inset -6px 6px 10px rgba(0, 0, 0, 0.1);

}

.profile svg{
  width: 20px;
  height: 20px;
  transform: rotate(180deg);
  fill: #303030;
  transition: transform 0.4s ease;
}

/* .profile svg:hover{
  margin-top: -16px;
  transform: rotate(0deg);
} */

.profile img{
  border-radius: 50%;
  background-color: white;
  width: 50px;
  height: 50px;
}

.profile_menu{
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 100%;
  padding: 12px 5px 8px 5px;
  z-index: 5;
  background-color: white;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 4px 10px 0 rgb(0 0 0/20%);
  transition: 0.2s all ease;
  width: 220px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
}

.profile_menu.active{
  opacity: 1;
  pointer-events: all;

}

.burger_menu{
  position: relative;
  width: 44px;
  background: rgb(79, 15, 89);
  height: 4px;
  cursor: pointer;
  display: none;
  transition: all 0.2s ease;
}

.burger_menu::before{
  content: "";
  position: absolute;
  top: -15px;
  left: 0;
  width: 100%;
  background: rgb(79, 15, 89);
  height: 4px;
}

.burger_menu::after{
  content: "";
  position: absolute;
  bottom: -15px;
  left: 0;
  width: 100%;
  background: rgb(79, 15, 89);
  height: 4px;
}

.burger_menu.active{
  height: 0;
  border: 0;
  transform: rotate(90deg);
}

.burger_menu.active::before{
  transform: rotate(-45deg);
  bottom: 11px;
}


.burger_menu.active::after{
  transform: rotate(45deg);
  bottom: 11px;
}

.container{
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 120px;
  display: flex;
  gap: 60px;

}

.left_menu{
  display: flex;
  flex-direction: column;
  padding-left: 12px;
  gap: 20px;
}

.menu_item{
  display: flex;
  align-items: center;
  gap: 5px;
}

/* .menu_item svg{
  fill: purple;
  stroke: purple;
} */

.main_wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  flex: 1;
  background-color: white;
  border-radius: 10px;
  padding-top: 30px;
  height: calc(100vh - 120px);
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;

}

.main_wrapper::-webkit-scrollbar{
  display: none;
}


.blank_container{
  width: 60%;
  height: calc(100% - 80px);
  flex: 0 0 calc(100% - 80px);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 32px;
  background-color: rgb(254, 216, 255);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.1);
  scroll-snap-align: center;
  transition: all 1s ease;
}

.blank_container.greeting{
  height: 200px;
  flex: 0 0 200px;
  background: none;
  box-shadow: none;
  font-size: 28px;
  text-align: center;

}


@media screen and (max-width: 1100px) {

  body{
    background: white;
  }

  .header_content{
    padding: 0 40px;
  }

  .left_menu{
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #9754CB;
    width: 100%;
    height: 80px;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    border-radius: 26px 26px 0 0;
  }

  .left_menu span{
    color: white;
    font-size: 18px;
  }

  .left_menu svg{
    width: 50px;
    height: 50px;
  }

  .left_menu path{
    fill: white;
  }

  .main_wrapper{
    /* background: none; */
    border-radius: 0;
    scroll-snap-align: start;
  }


  .container{
    padding-top:80px;
    margin-bottom: 80px;
  }

  .blank_container:last-child{
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 700px) {
  .left_menu span{
    display: none;
  }

  .blank_container{
    width: 70%;
  }

  .blank_container.greeting{
    font-size: 22px;
  }

  .profile{
    display: none;
  }

  .nav-menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #d8e5f7;
    z-index: 2;
    margin-top: 80px;
    transform: translateY(calc(-100vh - 80px));
    transition: all 0.3 ease;
  }

  .nav-menu.active{
    transform: translateY(0);
  }

  .burger_menu{
    display: block;
  }

  .logo img{
    max-width: 140px;
    max-height: 40px;
  }


}

@media screen and (max-width: 450px) {
  .left_menu{
    height: 60px;
  }

  .left_menu svg{
    width: 30px;
    height: 30px;
  }

  .nav-menu{
    margin-top: 70px;
  }

  .container{
    margin-bottom: 60px;
    padding-top: 70px;
  }

  .header{
    height: 70px;
  }
}